import Typography from '@material-ui/core/Typography';

import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import { InputLabel } from '@material-ui/core'
import Grid from '@material-ui/core/Grid';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import '../../App.scss';
import CoinList from '../../stores/CryptoList'
import {observer} from 'mobx-react-lite'
import { useEffect,useState } from 'react';
import cryptoList from '../../stores/CryptoList';
import './CryptoConvertor.css'

const useStyles = makeStyles((theme: Theme) =>
createStyles({
  root: {
    
    padding: theme.spacing(2),
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectLabel:{
    marginLeft:'5px'
  },
  table: {
    minWidth: 650,
  },
  ButtonStyle:{
    margin:' 50px auto',
    textAlign:'center'
  },
  disabledButton:{
    opacity:.5
  },

  
}),
);


const ConvertorForm:React.FC = observer(() => {
  const [defValue,setDefValue] = useState('BTC')
  const [firstValue,setFirstValues] = useState<any>(0)
  const [secondValue,setSecondValues] = useState<any>(0)
  const classes = useStyles()
    return (
        <div>
                 <Paper className={classes.paper}>
            <div>
<Grid container spacing={1} style={{alignItems:"center"}}>

    <TextField 
    
    disabled={(cryptoList.firstValue.name&&cryptoList.secondValue.name)===''?true:false}
    placeholder={(cryptoList.firstValue.name)===''?'валюта':''}
    type='number'
   
    onChange={(e)=>setFirstValues(e.target.value)}
         InputProps= {{  inputProps: {
          pattern: "[0-9]*"
        }}}
     style={{marginRight:'30px', maxWidth:'100px' }} 
   />

 
 
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
              
               
              >
                {cryptoList.NewList.map(item=>{
                  
                  if(item.Name===cryptoList.firstValue.name){
                    return   <MenuItem autoFocus={true}  selected onClick={()=>cryptoList.setFirstValue(item)} value={item.Name}>{item.FullName}</MenuItem>
                  } 
                  return  <MenuItem autoFocus={false} onClick={()=>cryptoList.setFirstValue(item)} 
                  value={item.Name}>{item.Name}</MenuItem>
                  
                })}
              </Select>
                <InputLabel style={{marginLeft:'5%',fontSize:'14px'}} id="demo-simple-select-label">{cryptoList.firstValue.name}</InputLabel>

</Grid >
<Grid container spacing={1} style={{alignItems:"center"}}>
  <TextField
      disabled={(cryptoList.firstValue.name&&cryptoList.secondValue.name)===''?true:false}
      placeholder={(cryptoList.secondValue.name)===''?'валюта':`${(firstValue*(cryptoList.firstValue.price/cryptoList.secondValue.price)).toFixed(3)} `}
   onChange={(e)=>setSecondValues((e.target.value))}
    value={firstValue?`${Number(firstValue*(cryptoList.firstValue.price/cryptoList.secondValue.price)).toFixed(3)} `:''} 
    style={{marginRight:'30px',maxWidth:'100px' }} 
    type='number'
    ></TextField>
 

              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
              
               
              >
                
                  {cryptoList.NewList.map(item=>{
                  return(
                      <MenuItem onClick={()=>{cryptoList.setSecondValue(item)
                      setDefValue(item.FullName)
                      }} value={item.FullName}>{item.FullName}</MenuItem>
                  )
                })}
                
              </Select>

              <InputLabel style={{marginLeft:'5%', fontSize:'14px'}}  id="demo-simple-select-label">{cryptoList.secondValue.name}</InputLabel>

 
</Grid >
<Typography style={{marginTop:'20px', textAlign:'left'}}  variant="h6" component="h5">
{firstValue?`${cryptoList.secondValue.FullName}: ${(firstValue*(cryptoList.firstValue.price/cryptoList.secondValue.price)).toFixed(3) }  `:''}
</Typography>
            </div>
          </Paper>
        </div>
    );
})

export default ConvertorForm;