diffArr =(arr1:Tcoin[],arr2:Tcoin[])=> {
    this.DiffList=arr1.map((item:Tcoin,i)=>{
        if(item.PRICE>arr2[i].PRICE){
          
            return  item.PRICE-arr2[i].PRICE
        }
        else if(item.PRICE<arr2[i].PRICE){
         
            return item.PRICE-arr2[i].PRICE
          }
          else{  
         
            return 0
          }
  
    })
     }


